.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}

.head-image{
    width: auto;
    transition: max-width 1s,min-width 1s ;
}

.head-text{
    font-family:'Hachi Maru Pop',cursive;
    margin: 15px 0px 15px 0px;
    transition: font-size 1s;
}

.head-image-expanded{
    min-width: 220px;
    max-width: 250px;
}

.head-image-contracted{
    max-width: 60px;
    min-width: 40px;
}

.head-text-expanded{
    font-size: 36px;
}

.head-text-contracted{
    font-size: 15px;
}