.searchbox-container{
    display: flex;
    justify-content: center;
    margin: 10px;
}
.searchbox-input{
    padding: 8px 15px;
    font-size: 250px;
    width: 250px;
    border-radius: 50px;
    border-width: 0;
    background: #a592f2;
    color:#e7e9f0;
    font-size: 15px;
    border-style: hidden;
    box-shadow: 0 0 5px rgba(255,255,255,0.5);
}

.searchbox-input::placeholder{
    color: #e7e9f0;
}

.searchbox-input::focus{
    outline: none;
}